<template>
	<div class="page-login">
	  <!-- <div class="load">
		  <img src="@/assets/images/mentos.gif" alt="">
	  </div> -->
	  <div class="layout">
		<header-com :index="-1" :scrollTop="scrollTop"></header-com>
		<div class="flogin">
		  <div class="wrap">
			<div
			  class="fpitl flog wow fadeInUp"
			  data-wow-delay="0.2s"
			  data-wow-duration="1s"
			>
			  <h3>Log in！</h3>
			  <i></i>
			</div>
			<div
			  class="floginvia wow fadeInUp"
			  data-wow-delay="0.3s"
			  data-wow-duration="1s"
			>
			  <div class="ficon">
				<img src="@/assets/images/fico19.png" alt="" />
			  </div>
			  <div class="teitl">
				<h2 :class="isEmail ? 'on' : ''" @click="onEmail">
				  Login via Email
				</h2>
				<h2 :class="!isEmail ? 'on' : ''" @click="onAccount">
				  Login via Discord
				</h2>
			  </div>
			  <div>
				<!-- <el-button type="primary">主要按钮</el-button> -->
			  </div>
			  <div v-show="isEmail" class="femail">
				<div class="femil">
				  <form action="" method="post">
					<div class="list">
					  <ul>
						<li>
						  <label>Email</label>
						  <input
							type="text"
							class="text"
							v-model="email"
							placeholder="Enter your email"
						  />
						</li>
						<li>
						  <label>Password</label>
						  <input
							type="text"
							v-model="password"
							class="text"
							placeholder="Password"
						  />
						</li>
					  </ul>
					</div>
					<a href="#" class="forget">Forget password</a>
					<div @click="submit" class="sub" value="login"></div>
					
					<router-link to="/createAccount" class="fcreate"><i>Create an account</i></router-link>
				  </form>
				</div>
			  </div>
			  <div v-show="!isEmail" class="femail">
				<div class="femil">
				  <form action="" method="post">
					<div class="list">
					  <ul>
						<li>
						  <label>Name</label>
						  <input
							type="text"
							class="text"
							v-model="name"
							placeholder="Enter your name"
						  />
						</li>
						<li>
						  <label>Password</label>
						  <input
							type="text"
							v-model="password"
							class="text"
							placeholder="Password"
						  />
						</li>
					  </ul>
					</div>
					<a href="#" class="forget">Forget password</a>
					<!-- <input type="submit" class="sub" @click="submit" value="login" /> -->
					<div @click="submit" class="sub" value="login"></div>
					<div @click="toUrl" class="fcreate"><i>Create an account</i></div>
				  </form>
				</div>
			  </div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
  </template>
  
  <script >
  import headerCom from "@/components/header";
  import Cookies from 'js-cookie'
//   import {  login } from '@/api/login'
  import {  login } from '../api/login'
  
  
  export default {
	name: "login",
	components: {
	  headerCom,
	},
	data() {
	  return {
		scrollTop: 0,
		password: '',
		email: '',
		name: '',
		isEmail: 1,
	  };
	},
	updated() {},
	mounted() {
	  new this.$wow.WOW().init();
	  window.addEventListener("scroll", this.handleScroll);
	},
	methods: {
		toUrl (){
			this.$router.push({path: '/createAccount'});
		},
	  handleScroll() {
		let scrollTop =
		  window.pageYOffset ||
		  document.documentElement.scrollTop ||
		  document.body.scrollTop; // 滚动条偏移量
		this.scrollTop = scrollTop;
	  },
	  onAccount() {
		this.isEmail = false;
		this.email = '';
	  },
	  onEmail() {
this.isEmail = 1;
this.name = '';
	  },
	  
	  submit() {
			let params = {
			  "username": this.name.length ? this.name : this.email,
			  password:this.password,
	  "code": "1234",
	  "captcha_id": "121313",
	  "captcha": "aadv"
			}
			console.log(Cookies.get("sessionid"),Cookies.get("yy"))
			Cookies.remove("sessionid")
			// this.$cookie.remove("username")
			login(params,(res) => {
        this.$message.success('Login succeeded')
		this.$router.push({path: '/product'});
		// this.$router.push({path:'/dashboard'})
                  }, (err) => { this.$message.warning(err.msg); })
	  },
	},
  };
  </script>
  <!-- <style scoped >
	
  </style> -->
  <style lang="scss" scoped>
  .layout {
	background: url(../assets/images/fbj1.jpg) no-repeat center/cover;
  }
  .floginvia {
	background: url(../assets/images/fimg5.jpg) no-repeat center/cover;
  }
  .page-login {
	.femil {
	  padding: 20px;
	  @include themeify {
		background-color: themed("bg-color");
	  }
	}
  }
  .fcreate {
	  cursor: pointer;
  }
  </style>